.app {
  box-sizing: border-box;
  position: relative;

  // max-width: 600px;
  // margin: 0 auto;
}
.header {
  box-sizing: border-box;
  max-height: 375px;
  height: 61vw;
  padding: 16px;
  background-color: #FFBC53;
}

.fact {
  display: flex;
  justify-content: space-between;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  color: white;
  &__weather {
    font-size: 48px;
    line-height: 56px;
  }

  &__condition {
    font-size: 24px;
    line-height: 28px;
  }
}

.term {
  font-size: 18px;
  line-height: 21px;
  font-weight: normal;
}

.forecast-day {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  color: white;
  background-color: #AEBFD1;
  margin-bottom: 1px;
  box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);

  &__header {
    box-sizing: border-box;
    display: flex;
    font-size: 24px;
    line-height: 28px;
    padding-left: 16px;
    height: 64px;
    //border: 1px solid green;
    box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);
  }

  &__column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 50%;
    //border: 1px solid red;
  }

  &__arrow {
    width: 12px;
    height: 12px;
    //border: 1px solid green;
    margin-right: 12px;
  }

  &__icon {
    width: 33.3%;
  }

  &__max {
    width: 33.3%;
  }

  &__min {
    width: 33.3%;
  }
}

.arrow-enter {
  transform: rotate(0deg);
}

.arrow-enter-active {
  transform: rotate(180deg);
  transition: transform linear 300ms;
}

.arrow-enter-done {
  transform: rotate(180deg);
}

.arrow-exit {
  transform: rotate(180deg);
}

.arrow-exit-active {
  transform: rotate(360deg);
  transition: transform linear 300ms;
}
