.forecast__row-full {
  box-sizing: border-box;
  //border: 1px solid blue;
  max-height: 370px;
  height: 61vw;
  cursor: pointer;
  //background-color: red;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
}

/* forecast__row-full */
.row-full-enter {
  height: 0;
  opacity: 0;
}

.row-full-enter-active {
  opacity: 1;
  height: 370px;
  transition: 300ms linear;

  @media (max-width: 600px) {
    height: 61vw;
  }
}

.row-full-exit {
  opacity: 1;
  height: 370px;
  @media (max-width: 600px) {
    height: 61vw;
  }
}

.row-full-exit-active {
  height: 0;
  opacity: 0;
  transition: 300ms linear;
}

.row__item {
  display: flex;
  // justify-content: center;
  align-items: center;
}